/**
 * 
 * routes: [] 
 * initializationTabs: [] tabbar
 * path: 
 * component: 
 * indexRoute:  MatchCell
 * rediret: 
 * state: Location   : 
 * mark:   : bowserHistory
 * childRoutes: 
 *checkAuthority:     true
 */

export const routerConfig = {
  routes: [
    {
      path: '/',
      component: 'App',
      indexRoute: { redirect: '/home' },
      childRoutes: [
        {
          path: '/home',
          component: 'home',
          state: {
            mark: '',
          },
        },
      ],
    }, { // 
      path: '*',
      component: 'exception/404',
      state: {
        checkAuthority: false,
      },
    },
  ],
  initializationTabs: [
    {
      pathname: '/home',
      state: {
        mark: '',
      },
    },
  ],
};



